<style>
	.filter {
	  position: relative;
	  display: flex;
	  padding: 10px 0 0 28px;
	  font-size: 12px;
	  line-height: 11px;
	  color: #fff;
	  z-index: 9999;
	}
	.filter input {
		background: transparent;
		border: none;
		color: #fff;
	}
	.timeText {
		opacity: 0.69;
		padding-right: 7px;
	}
	.startTime {
		display: inline-block;
	}
	.endTime {
		display: inline-block;
		padding-left: 42px;
	}
	.filter .el-date-editor {
		width: 55%;
	}
	.products {
		position: absolute;
		display: inline-block;
		right: 15px;
	}
	.products .pro {
		float: right;
		padding: 2px 25px 2px 2px;
	}
	.products .pro .arrow {
		position: absolute;
		width: 6px;
		height: 6px;
		margin-left: 10px;
		border-left: 2px solid #fff;
		border-bottom: 2px solid #fff;
		transform: rotate(-45deg);
	}
	.products .all {
		display: inline-block;
		margin-right: 18px;
	}
	.pro_list {
		position: absolute;
		right: 22px;
		width: 102px;
		text-align: left;
		background-color: #1e3642;
		font-size: 14px;
		margin-top: 22px;
		overflow: hidden;
		z-index: 9;
	}
	.pro_list li {
		height: 36px;
		line-height: 35px;
		border-bottom: 1px solid rgba(255, 255, 255, 0.1)
	}
	.pro_list li:last-child {
		border: none;
	}

	.pro_name {
		position: absolute;
		display: inline-block;
		right: 8px;
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
		width: 70px;
	}

</style>

<template>
	<div class="filter">
		<div class="startTime">
			<span class="timeText">起始时间</span>
			<el-date-picker type="date" placeholder="选择日期" v-model='startDate'></el-date-picker>
			<div class="myCalendar"></div>
		</div>
		<div class="endTime">
			<span class="timeText">结束时间</span>
			<el-date-picker type="date" placeholder="选择日期" v-model='endDate'></el-date-picker>
			<!-- <div class="myCalendar"></div> -->
		</div>
		<div class="product-wrapper" v-show="showProduct">
			<div class="products">
				<div class="pro" @click="showProPanel()">
					产品<i class="arrow"></i>
				</div>
				<div class="all" v-show="pro_filter_flag" @click="selectAll()"><checkbox :isChecked="selectAll_flag"></checkbox>全选</div>
			</div>
			<div class="pro_list" v-show="pro_filter_flag">
				<ul>
					<li v-for="(pro, index) in pro_list" @click="pro_toggle(pro)">
						<checkbox :isChecked="pro.selected"></checkbox><span class="pro_name">{{pro.name}}</span>
					</li>
				</ul>
			</div>

			<div class="el"></div>

		</div>
	</div>
</template>

<script>
	import checkbox from 'components/checkbox/checkbox'
	export default {
		props: {
			myChart: {
				type: Object,
				default: {}			
			}
		},
		data() {
			return {
				startDate: '2015.12.04',
				endDate: '2016.01.15',
				pro_list: [],
				option: {},
				showProduct: false,
				pro_filter_flag: false,
				selectAll_flag: true
			}
		},
		components: {
			checkbox
		},
		mounted() {
			this.filter_init()
		},
		methods: {
			filter_init() {
				this.prevClass = this.$parent.$el._prevClass
				this.option = this.myChart.getOption()
				this.resetOption = this._deepCopy(this.myChart.getOption())
				this._initProList()
			},
			_deepCopy(obj) {
				let str, newobj;
				str = newobj = obj.constructor === Array ? [] : {};
				if(typeof obj !== 'object') {
					return
				} else if (window.JSON) {
					str = JSON.stringify(obj)
					newobj = JSON.parse(str)
				} else {
					for (var i in obj) {
						newobj[i] = typeof obj[i] === 'object' ? cloneObj(obj[i]) : obj[i]
					}
				}
				return newobj;
			},
			_initProList() {
				let arr = []
				if(this.prevClass !== "point") {
					this.showProduct = true
					this.option.xAxis[0].data.forEach((pro, index) => {
						arr.push({
							name: pro,
							selected: true
						})
					})
					this.pro_list = arr
				}
			},
			showProPanel() {
				this.pro_filter_flag = !this.pro_filter_flag
			},
			selectAll() {
				this.selectAll_flag = !this.selectAll_flag
				this.pro_list.forEach((pro) => {
					pro.selected = this.selectAll_flag
				})
				this.redraw()
			},
			pro_toggle(pro) {
				pro.selected = !pro.selected
				this.selectAll_flag = this.isSelectAll()
				this.redraw()
			},
			isSelectAll() {
				let flag = true
				for(var i = 0; i < this.pro_list.length; i++) {
					if(this.pro_list[i].selected == false) {
						flag = false
					}
				}
				return flag
			},
			redraw() {
				let option = this._deepCopy(this.resetOption)
				let count = 0
				this.pro_list.forEach((pro, index) => {
					// console.log(index)
					index = index - count
					console.log(index)
					console.log(pro.selected)
					if(pro.selected == false) {
						option.xAxis[0].data.splice(index, 1)
						console.log(option.xAxis[0].data)
						option.series.forEach((obj) => {
							obj.data.splice(index, 1)
						})
						count++
					}
				})
				this.myChart.setOption(option)		
				// console.log(option)		
			}
		}
	}
</script>

