<style scoped>
	.point {
		/*height: 800px;*/
		background: url('~assets/img/bg.jpg') no-repeat;
		background-size: 100% 100%;
		/*color: #fff;*/
	}
	.main {
		width: 100%;
		height: calc(100% - 100px);
		/*margin-top: -15px;*/
		transition: all 0.5 linear
	}	
</style>

<template>
	<div class="point">
		<v-header :myChart="myChart" :name="name" :legendArr="legendArr"/>
		<v-filter :myChart="myChart" v-if="myChart._dom"/>
		<div class="main">point</div>
	</div>
</template>

<script>
	import axios from 'axios'
	import china from 'echarts/map/js/china.js'
	import echarts from 'echarts'
	import header from 'components/header/header'
	import filter from 'components/filter/filter'

	export default {
		data() {
			return {
				geoCoordMap: {},
				myChart: {},
				legendArr: [],
				name: '散点图'
			}
		},
		created() {
			this._getCityData()
		},
		methods: {
			_getCityData() {
				axios.get('./static/data/cityData.json').then(res => {
					this.geoCoordMap = res.data
					this.$nextTick(() => {
						this._getMychart()
					})
				})
			},
			_getMychart() {
				axios.get('/static/data/point/testData.json').then(res => {
					let option = {
						title: {
							show: false
						},
						tooltip: {
							trigger: 'item',
							formatter: function(params) {
								return params.name + ':' + params.value[2]
							}
						},
						legend: {
							show: false
						},
						visualMap: {
							min: 0,
							max: 200,
							bottom: 50,
							splitNumber: 5,
							inRange: {
								color: ['#255B78', '#2A7484', '#2F9696', '#3BBCB0', '#51D4EB']
							},
							textStyle: {
								color: '#fff'
							}
						},
						geo: {
							type: 'map',
							map: 'china',
							label: {
								emphasis: {
									show: false
								}
							},
							zoom: 1,
							top: 50,
							itemStyle: {
								normal: {
									color: '#3c4247',
									opacity: 0.6,
									borderColor: 'rgba(255, 255, 255, 0.35)'
								},
								emphasis: {
									color: '#2a333d'
								}
							}
						},
						series: [{
							name: '标签1',
							type: 'scatter',
							coordinateSystem: 'geo',
							symbolSize: function(val) {
								return val[2] / 6
							},
							label: {
								normal: {
									show: false
								},
								emphasis: {
									show: false
								}
							},
							itemStyle: {
								emphasis: {
									borderColor: '#fff',
									borderWidth: 1
								}
							},
							data: this.converData(res.data)
						},{
							name: '标签2',
							type: 'scatter',
							coordinateSystem: 'geo',
							symbolSize: function(val) {
								return val[2] / 6
							},
							label: {
								normal: {
									show: false
								},
								emphasis: {
									show: false
								}
							},
							itemStyle: {
								emphasis: {
									borderColor: '#fff',
									borderWidth: 1
								}
							},
							data: this.converData(res.data)
						},{
							name: '标签3',
							type: 'scatter',
							coordinateSystem: 'geo',
							symbolSize: function(val) {
								return val[2] / 6
							},
							label: {
								normal: {
									show: false
								},
								emphasis: {
									show: false
								}
							},
							itemStyle: {
								emphasis: {
									borderColor: '#fff',
									borderWidth: 1
								}
							},
							data: this.converData(res.data)
						}]	
					}
					this.point_init(option)
				})
			},
			point_init(option) {
				this.myChart = echarts.init(document.querySelector('.point .main'))
				this.myChart.setOption(option)
				this.legendArr = this.myChart.getOption().series
				this.legendArr.forEach(function(item) {
					item.selected = true
				})
				window.addEventListener('resize', function() {
					this.myChart.resize()
				}.bind(this))				
			},
			converData(data) {
				let res = []
				for (let i = 0; i < 4; i++) {
					let l = data.length
					let x = parseInt(Math.random() * l)
					let geoCoord = this.geoCoordMap[data[x].name]
					if(geoCoord) {
						res.push({
							name: data[x].name,
							value: geoCoord.concat(Math.random() * 200)							
						})
					}
				}
				return res
			}
		},
		components: {
			'v-header': header,
			'v-filter': filter
		}
	}
</script>
