<style scoped>
	.title {
	  position: relative;
	  display: flex;
	  height: 50px;
	  line-height: 50px;
	  background-color: rgba(32, 32, 35, 0.2);
	  color: white;
	  width: 100%;	
	}
	h1 {
    flex: 0 0 120px;
    font-size: 21px;
    font-weight: bold;
    padding-left: 20px;		
	}
	ul {
    position: absolute;
    right: 0;
    padding-right: 20px;
    margin-top: -2px;		
	}
	li {
    display: inline-block;
    min-width: 59px;
    padding: 2px 10px 2px 10px;
    line-height: 20px;
    text-align: center;
    font-size: 11px;		
	}
	li:first-child {
		background-color: #fff;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
	}
	li:last-child {
		background-color: #fff;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;		
	}
	li:hover {
		cursor: pointer;
	}
</style>

<template>
	<div class="title">
		<h1>{{name}}</h1>
		<ul>
			<li v-for="(legend, index) in legendArr" :style="styleArr[index]" @click="legendToggle(legend, index)" @mouseover="hightLight(index)" @mouseout="downplay(index)">{{legend.name}}</li>
		</ul>
	</div>
</template>

<script>
	export default {
		props: {
			legendArr: {
				type: Array,
				default: []
			},
			name: String,
			myChart: Object,
		},
		data() {
			return {
				color: this.$store.state.color,
				styleArr: []
			}
		},
		created() {
			this.header_init()
		},
		methods: {
			header_init() {
	      this.color.forEach((color) => {
	        this.styleArr.push({
	          background: color,
	          border: '1px solid' + color
	        })
	      })
			},
			legendToggle(legend, index) {
				legend.selected = !legend.selected
				this.myChart.dispatchAction({
					type: "legendToggleSelect",
					name: legend.name
				})
				this.changeStyle1(legend, index)
			},
			changeStyle1(legend, index) {
				if(legend.selected) {
					this.styleArr[index].background = this.color[index]
					this.styleArr[index].border = '1px solid' + this.color[index]
				} else {
					this.styleArr[index].background = 'transparent'
					this.styleArr[index].border = '1px solid #9c8c84'
				}
			},
			hightLight(index) {
				this.myChart.dispatchAction({
					type: "highlight",
					seriesIndex: index
				})
			},
			downplay(index) {
				this.myChart.dispatchAction({
					type: "downplay",
					seriesIndex: index
				})
			}
		}
	}
</script>

