import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import {DatePicker} from 'element-ui'
Vue.config.productionTip = false

Vue.component(DatePicker.name, DatePicker)

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')

router.push('dashboard')