<template>
	<div class="columnChart">
		<v-header :name="name" :legendArr="legendArr" :myChart="myChart" :shiyan="shiyan"/>
		<v-filter :myChart="myChart" v-if="myChart._dom"/>
		<div class="main"></div>
	</div>
</template>

<script>
	import header from 'components/header/header'
	import filter from 'components/filter/filter'
	import echarts from 'echarts'

	export default {
		data() {
			return {
				legendArr: [],
				color: this.$store.state.color,
				myChart: {},
				name: '柱状图',
				shiyan: 'shiyan'
			}
		},
		components: {
			'v-header': header,
			'v-filter': filter
		},
		methods: {
			column_inti() {
				this.legendArr = this.myChart.getOption().series
				this.legendArr.forEach(function(item) {
					item.selected = true
				})
				window.addEventListener('resize', function() {
					this.myChart.resize()
				}.bind(this))
			}
		},
		mounted() {
			this.myChart = echarts.init(document.querySelector('.columnChart .main'))
			var option = {
				tooltip: {
	        trigger: 'axis'
	      },
	      legend: {
	        // 这里的图例，没有自动获取series的name，不知道为什么
	        show: false,
	        // data: ['product1', 'product2'],
	        textStyle: {
	          color: '#fff'
	        }
	      },
	      color: this.color,
	      xAxis: [{
	        name: '产品',
	        type: 'category',
	        axisLine: {
	          show: false
	        },
	        axisTick: {
	          show: false,
	          lineStyle: {
	            color: '#fff'
	          }
	        },
	        nameTextStyle: {
	          color: 'rgba(255, 255, 255, 0.69)'
	        },
	        axisLabel: {
	          textStyle: {
	            color: "#fff"
	          }
	        },
	        data: ['产品一', '产品二']
	      }],
	      yAxis: [{
	        axisLine: {
	          show: false
	        },
	        nameLocation: 'end',
	        nameGap: 20,
	        axisTick: {
	          show: false,
	          lineStyle: {
	            color: '#fff'
	          }
	        },
	        splitLine: {
	          lineStyle: {
	            color: ['rgba(230, 230, 230, 0.2)']
	          }
	        },
	        axisLabel: {
	          textStyle: {
	            color: 'white',
	            fontSize: 14
	          }
	        },
	        name: '数量',
	        type: 'value',
	        nameTextStyle: {
	          color: 'rgba(255, 255, 255, 0.69)'
	        }
	      }],
	      series: [{
	        name: 'product1',
	        type: 'bar',
	        data: [2.0, 4.9],
	        barWidth: 16,
	        barGap: 0
	      }, {
	        name: 'product2',
	        type: 'bar',
	        data: [2.6, 5.9],
	        barWidth: 16,
	        barGap: 0
	      }, {
	        name: 'product3',
	        type: 'bar',
	        data: [2.0, 6.4],
	        barWidth: 16,
	        barGap: 0
	      }, {
	        name: 'product4',
	        type: 'bar',
	        data: [4.0, 5.9],
	        barWidth: 16,
	        barGap: 0
	      }, {
	        name: 'product5',
	        type: 'bar',
	        data: [5.6, 4.9],
	        barWidth: 16,
	        barGap: 0
	      }, {
	        name: 'product6',
	        type: 'bar',
	        data: [2.0, 3.4],
	        barWidth: 16,
	        // barGap只有放最后一个bar里才生效，且对坐标系里所有的都生效。
	        barGap: 0
	      }]
			}
			this.myChart.setOption(option)
			this.column_inti()
		}
	}
</script>

<style scoped>
	.columnChart {
		height: 800px;
		background: url('~assets/img/bg.jpg') no-repeat;
		background-size: 100% 100%;
		color: #fff;
	}
	.main {
		width: 100%;
		height: calc(100% - 100px);
		margin-top: -15px;
	}
</style>
